  top: -5.5em;
  margin-left: -1em;
  z-index: -1;
}
@media (min-width: 782px) {
  #paperAirplane {
    z-index: 0;
    width: 300px;
    height: 300px;
    position: relative;
    top: 0;
    margin-left: 0;
    margin-bottom: -300px;
    transform: translate(-300px, -80px);
  }
}

/* Single Pages & Courses */
.arca-page-header {
  overflow: hidden;
}
.arca-page-header .wp-block-cover {
  position: relative;
  min-height: calc(400px + 5vw);
  -webkit-clip-path: polygon(0px 0px, calc(100% - (3rem + 2vw)) 0%, 100% 30%, calc(100% - (5rem + 3vw)) 100%, 0px 100%);
  clip-path: polygon(0px 0px, calc(100% - (3rem + 2vw)) 0%, 100% 30%, calc(100% - (5rem + 3vw)) 100%, 0px 100%);
}
.arca-page-header .wp-block-cover:before {
  content: "";
  position: absolute;
  background: #a897c0;
  opacity: 0.6;
  transform: skew(-21deg);
  top: 0;
  bottom: 0;
  left: 75%;
  right: 15%;
  z-index: 1;
}
.arca-page-header .wp-block-cover:after {
  content: "";
  position: absolute;
  background: #4F2D7F;
  opacity: 0.6;
  transform: skew(-21deg);
  top: 0;
  bottom: 0;
  left: 85%;
  right: 0;
  z-index: 2;
}
.arca-page-header .wp-block-column:nth-of-type(2) {
  padding: 1em 8% 1em 3%;
}
.arca-page-header h1, .arca-page-header .h1 {
  font-size: calc(2em + 1vw);
}
.arca-page-header .yoast-breadcrumbs {
  margin: -1em 0 1.5em 0;
  color: #687176;
}
.arca-page-header .yoast-breadcrumbs a {
  color: #687176;
}

.arca-course-header {
  overflow: hidden;
}
.arca-course-header .wp-block-cover {
  position: relative;
  min-height: calc(400px + 5vw);
  -webkit-clip-path: polygon(0 0, 75% 0, 100% 33%, 100% 67%, 75% 100%, 0 100%);
  clip-path: polygon(0 0, 75% 0, 100% 33%, 100% 67%, 75% 100%, 0 100%);
}
.arca-course-header .wp-block-column:nth-of-type(2) {
  padding: 1em 8% 1em 3%;
}
.arca-course-header h1, .arca-course-header .h1 {
  font-size: calc(2em + 1vw);
}
.arca-course-header .yoast-breadcrumbs {
  margin: -1em 0 1.5em 0;
